import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-view-article',
  templateUrl: './view-article.component.html',
  styleUrls: ['./view-article.component.scss']
})
export class ViewArticleComponent {

  Ques: any = this.ar.queryParams;
  users: any;

  constructor(private ar: ActivatedRoute, private router: Router) {
    this.users = JSON.parse(localStorage.getItem('userdetails') || '');
  }
  ngOnInit() {
    var a = this.Ques['_value']
    const keys = Object.keys(a);

    if (this.Ques['_value']) {
      if (this.users) {
        if (this.users.user_type == 5 || this.users.user_type == 1 || this.users.user_type == 2) {
          this.router.navigate([`/admin/knowledgebase`], { queryParams: { 'ques': keys[0] } })
        }
        else if (this.users.user_type == 3 || this.users.user_type == 4) {
          this.router.navigate([`/knowledgebase`], { queryParams: { 'ques': keys[0] } })
        }
      }
      else {
        this.router.navigateByUrl('');
      }
    }
    else {
      this.router.navigateByUrl('');
    }
  }
}
